export function fetchGraphCompleteEpic(action$) {
  return action$.pipe(
    ofType(FETCH_GRAPH_COMPLETE),
    filter(({ payload }) => (
      payload &&
      payload.store &&
      payload.store.settings
    )),
    map(({ payload: { store: { settings } } }) => setStoreSettings(settings))
  );
}
